const routes = [
    {
        name: '词性分析',
        path: '/plate/wordAnaly',
        component:() => import('@/components/plate/wordAnaly')
    },
    {
        name: '词性分析2',
        path: '/plate/wordAnaly2',
        component:() => import('@/components/plate/wordAnaly2'),
        props($route){
            return {str_1:$route.query.str_1}
        }
    }
];

export default routes
