<template>
  <div id="left_guide">
    <ul>
        <li v-for="i in to_list" :key="i">
            <div @click="move_to($event, i)">
                <img src="" alt="显示图片">
                <span>{{i}}</span>
            </div>
        </li>
    </ul>
  </div>
</template>

<script>
export default {
    data(){
        return{
            to_list:['词性分析', '词频统计', '词云图绘制', 'word2vec', 'LDA']
        }
    },
    methods:{
        move_to(event, plate){
            console.log(plate);
            this.$router.push({
                name: plate
            })
        }
    }
}
</script>

<style>
    #left_guide{
        background-color: black;
        position: absolute;
        top: 100px;
        bottom: 0px;
        width: 250px;
        left: 0px;
        padding-top: 50px;
    }
    #left_guide span{
        color: aliceblue;
        font-size: 20px;
    }
    #left_guide div{
        height: 100px;
        line-height: 100px;
        cursor: pointer;
    }
</style>